$(".read-more").on("click", function (e) {
    e.preventDefault();

    var $this = $(this),
        $trigger = $this.find("a");

    $this.parent().toggleClass("summarized");
    $this.toggleClass("fade");

    if ($trigger.text().toLowerCase() === "بیشتر بخوانید" || $trigger.text().toLowerCase() === "بیشتر ببینید") {
        $trigger.text("نمایش کمتر");
        $trigger.removeClass("down-arrowhead");
        $trigger.addClass("up-arrowhead");
    } else {
        $trigger.text("بیشتر بخوانید");
        $trigger.removeClass("up-arrowhead");
        $trigger.addClass("down-arrowhead");
    }
});
