$(document).ready(function () {
    if ($('.more').height() > 162) {
        $('.more').addClass('add_more');
        $('.show-more').show();
    } else {
        $('.more').removeClass('add_more');
    }
});
function showMore() {
    if ($('.more').height() > 162) {
        $('.more').addClass('add_more');
        $('.show-more').text('نمایش بیشتر');
    } else {
        $('.more').removeClass('add_more');
        $('.show-more').text('نمایش کمتر');
    }
}

$('.change-selected-item').click(function (e) {
    e.preventDefault();
    var url = $(this).attr('href'),
        method = 'GET',
        id = url.split('/')[3];

    $.ajax({
        url : url,
        method : method,
        success : function (data) {
            $('#post-box').html(data);

            var table = $(".checkout-address-table"),
                item = $("#item-"+id);

            // console.log(data.data);
            // $(".checkout-address-table").find("input:radio").prop('checked',false);
            table.find("input:radio").prop('checked',false);
            $('#deliveryPreference_slow_'+id).prop("checked", true);
            table.find('.border-bottom').addClass('border-bottom-gray').removeClass('border-bottom-yellow');
            table.find('.border-top').addClass('border-top-gray').removeClass('border-top-yellow');
            table.find('.mobile').addClass('mobile-gray').removeClass('mobile-yellow');
            table.find('.city').addClass('city-gray').removeClass('city-yellow');
            table.find('.tel').addClass('tel-gray').removeClass('tel-yellow');
            table.find('.zipcode').addClass('zipcode-gray').removeClass('zipcode-yellow');
            table.find('.last').addClass('last-gray').removeClass('last-yellow');
            table.find('.first-row ').addClass('first-row-gray').removeClass('first-row-yellow');
            table.find('.name').addClass('name-gray').removeClass('name-yellow');
            table.find(".full-address, .city , .state, .txtright, .mobile, .tel, .zipcode").addClass('in-active');


            item.find('.border-bottom').removeClass('border-bottom-gray').addClass('border-bottom-yellow');
            item.find('.border-top').removeClass('border-top-gray').addClass('border-top-yellow');
            item.find('.mobile').removeClass('mobile-gray').addClass('mobile-yellow');
            item.find('.tel').removeClass('tel-gray').addClass('tel-yellow');
            item.find('.city').removeClass('city-gray').addClass('city-yellow');
            item.find('.zipcode').removeClass('zipcode-gray').addClass('zipcode-yellow');
            item.find('.last').removeClass('last-gray').addClass('last-yellow');
            item.find('.first-row ').removeClass('first-row-gray').addClass('first-row-yellow');
            $('.name-'+id).removeClass('name-gray').addClass('name-yellow');
            item.find(".full-address, .city , .state, .txtright, .mobile, .tel, .zipcode").removeClass('in-active');
        }
    });
});


$('body').on('click', '#share-alt',function (e) {
    e.preventDefault();
    $('body').find('#social-networks').toggleClass('hidden-xs hidden-sm');
});

